@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.Tech {
  margin-bottom: 5rem; }
  .Tech--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 3rem 0 1rem;
    line-height: 1; }
  .Tech--subHeading {
    font-weight: 400;
    margin: 1rem 0;
    line-height: 1;
    padding: 1rem 0;
    margin: 1rem 0; }
  .Tech--text-secondaryColor {
    color: orangered; }
  .Tech--services {
    text-align: center;
    letter-spacing: 0.3rem;
    margin: 3rem; }
  .Tech--skillsSection {
    padding: 2rem; }
  .Tech--skillsGrid {
    display: grid;
    grid-gap: 1.5rem;
    margin-top: 2rem; }
  .Tech--skillsHeading {
    font-size: 1.05rem;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    padding: 0 1rem;
    border: 0.05rem solid #111;
    border-radius: 1rem;
    background: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08); }
  .Tech--skillsBorder {
    border: 0.05rem solid #111;
    background: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
    border-radius: 0.5rem; }
    .Tech--skillsBorder-frontEnd {
      grid-area: fe; }
    .Tech--skillsBorder-backEnd {
      grid-area: be; }
    .Tech--skillsBorder-devOps {
      grid-area: do; }
  .Tech--skills {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap; }

@media screen and (min-width: 0px) {
  .Tech--skillsGrid {
    grid-template-columns: 1fr;
    grid-template-areas: "fe" "be" "do"; } }

@media screen and (min-width: 768px) {
  .Tech--skillsGrid {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "fe be" "do do"; } }

@media screen and (min-width: 1171px) {
  .Tech--skillsGrid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "fe be do"; } }
