@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.AboutMe {
  margin-bottom: 5rem; }
  .AboutMe--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 3rem 0 1rem;
    line-height: 1; }
  .AboutMe--subHeading {
    font-weight: 400;
    margin: 1rem 0;
    line-height: 1;
    padding: 1rem 0;
    margin: 1rem 0; }
  .AboutMe--text-secondaryColor {
    color: orangered; }
  .AboutMe--main {
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-gap: 0rem 1rem;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center; }
  .AboutMe--gridHeading {
    grid-area: gh;
    justify-self: left;
    margin: 0; }
  .AboutMe--image {
    grid-area: portrait;
    border-radius: 50%;
    border: solid 0.3rem orangered;
    margin: auto;
    width: 225px;
    height: 225px; }
  .AboutMe--bio {
    font-size: 1.2rem;
    grid-area: bio;
    text-align: left; }

@media screen and (min-width: 0px) {
  .AboutMe--main {
    grid-template-columns: 1fr;
    grid-template-areas: "portrait" "gh" "bio"; } }

@media screen and (min-width: 768px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, minmax(10px, 1fr));
    grid-template-areas: ". gh ." "portrait bio bio"; } }

@media screen and (min-width: 1171px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: ". gh ." "portrait bio bio"; } }
